import React, { Component } from 'react';

class Alert extends Component {
  constructor(props) {
    super(props);
    this.color = null;
  }

  getStyle = () => {
    return {
      color: this.color,
    };
  };

  render() {
    return (
      <div className="Alert">
        <p style={this.getStyle()}>{this.props.text}</p>
      </div>
    );
  }
}

class InfoAlert extends Alert {
  constructor(props) {
    super(props);
    this.color = `#2C58F0`;
  }
}

class ErrorAlert extends Alert {
  constructor(props) {
    super(props);
    this.color = `#F04E2C`;
  }
}

class OfflineAlert extends Alert {
  constructor(props) {
    super(props);
    this.color = `#FF8200`;
  }
}

export { InfoAlert, ErrorAlert, OfflineAlert };
